// Step 1: Import React
import * as React from 'react'
import Layout from '../components/layout'
import { StaticImage } from 'gatsby-plugin-image'
import  Seo from '../components/seo'
import ProjectCabaret from '../components/projcabaret'
import GameCabaret from '../components/gamecabaret'
import { useState, useEffect } from 'react';


// Step 2: Define your component
const IndexPage = () => {

  const [emoji , setEmoji] = useState(null);

  useEffect(() => {
    const emoji = ["😎", "👑", "🎓", "📚", "🗿", "🇮🇹", "📈"];
    setEmoji(emoji[Math.floor(Math.random() * emoji.length)])
  }, []);

  return (
    <Layout pageTitle="Home Page">

      <div className='d-flex justify-content-center'>
      <div className='d-flex align-items-center container-fluid row justify-content-between introsito' style={{padding: 0+ "px"}}>
        <div className="d-flex col-12 col-sm-6 order-sm-last justify-content-sm-end justify-content-center">
          <StaticImage alt='logo' src='../images/icon.webp' className='fimage'/>
        </div>
        <div className="col-12 col-sm-6 order-sm-first mt-4">
          <div className='justify-content-sm-between'>
            <h2><b>I'm Rick {emoji},</b></h2>
            <p>This is my own little web garden 🌱 .<br/> Software developer and book lover. I would need two lives to satisfy my curiosity </p>
            <div className="d-grid d-sm-flex gap-2 d-md-block mx-auto">
              <a href={"https://ricksrick.github.io/"} className="btn btn-primary me-md-2 my-first-button" type="button">Quiz Basi di Dati</a>
              <a href={"https://ricksrick.itch.io/"} className="btn btn-primary my-second-button" type="button">My itch.io</a>
            </div>
          </div>
        </div>
      </div>
      </div>

      <div className='d-flex justify-content-between mb-4'>
        <p style={{fontSize: 1.25 + "rem", marginBottom: 0 + "px"}}><b>Projects</b></p>
        <a href={"https://github.com/RickSrick?tab=repositories"} className="btn btn-primary my-first-button" type="button">View All</a>
      </div>

      <div className="row row-cols-1 row-cols-md-3 row-cols-sm-2 g-4">
        { <ProjectCabaret number={6}/> }
      </div>

      <div className='d-flex justify-content-between mt-4 mb-4'>
        <p style={{fontSize: 1.25 + "rem", marginBottom: 0 + "px"}}><b>Games</b></p>
        <a href={"https://github.com/RickSrick?tab=repositories"} className="btn btn-primary my-first-button" type="button">View All</a>
      </div>

      <div className="row justify-content-center row-cols-1 row-cols-md-3 row-cols-sm-2 g-4">
        { <GameCabaret number={1}/> }
      </div>

    </Layout>
  )
}

export const Head = () => (
  <>
    <html lang="en" />
    <Seo title="Home Page"/>
    <meta name="description" content="Personal site used as a portfolio in tech projects and videogame development"/>
  </>
)

export default IndexPage